.info-block-image {
    display: -ms-flexbox;
    display: flex;
}

.info-block-image-left,
.info-block-image-right {
    -ms-flex: 1 auto 0%;
        flex: 1 auto 0%;
    width: 50%;
    position: relative;
    height: calc(var(--cell) * 13);
}

.info-block-image-right {
    padding-top: calc(var(--cell));
}


.info-block-image-left img {
    position: absolute;
    top: 0;
    right: 0;
}

.info-block-image .info-block-header {
	margin-left: 160px;
}
.info-block-image h2 {
	margin-left: 160px;
}


