.about-page {
    background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAAtCAYAAABvewHEAAAABHNCSVQICAgIfAhkiAAAAAlwSFlzAAALEgAACxIB0t1+/AAAABx0RVh0U29mdHdhcmUAQWRvYmUgRmlyZXdvcmtzIENTNui8sowAAAAVdEVYdENyZWF0aW9uIFRpbWUANi8xMy8xN9IVNcwAAAAYSURBVAiZY/j//z8DEwMDw3Ak2C7MiwQAxisFIyPcW6IAAAAASUVORK5CYII=');
}
.about-page-header {
    padding: 90px 0;
    display: -ms-flexbox;
    display: flex;
}
.about-page-header-image {
    width: 50%;
    position: relative;
}
.about-page-header-image img {
    position: absolute;
    right: 0;
    top: 0;
}
.about-page-header-text {
    width: 50%;
    position: relative;
    padding-top: calc(var(--cell) * 2)
}
.about-page-header-text h1 {
    font-size: 111px;
    font-weight: 400;
    color: #05CF9D;
    margin-left: -100px;
    -webkit-font-smoothing: antialiased;
    -webkit-transform: translateY(8px);
        -ms-transform: translateY(8px);
            transform: translateY(8px);
}
.about-page-header-text p {
    margin: 0 auto;
	font-size: 23px;
	font-weight: 300;
	letter-spacing: 1.22px;
	line-height: 45px;
	width: 450px;
    -webkit-font-smoothing: antialiased;
    -webkit-transform: translateY(12px);
        -ms-transform: translateY(12px);
            transform: translateY(12px);
    padding-top: var(--cell);
}
.about-page-past {
	display: -ms-flexbox;
	display: flex;
	padding: var(--cell) 0 var(--cell) 240px;
}
.about-page-past-section {
	padding-right: 70px;
	//padding-top: 26px;
	width: 410px;
}
.about-page-past-section h3 {
    -webkit-font-smoothing: antialiased;
    letter-spacing: 3.98px;
    line-height: 45px;
	font-size: 48px;
	font-weight: 400;
	color: #EAEBF3;
    -webkit-transform: translateY(2px);
        -ms-transform: translateY(2px);
            transform: translateY(2px);
}
.about-page-past-section p {
    font-size: 15px !important;
    font-weight: 100;
    font-family: "sofia-pro";
    line-height: 33px;
    letter-spacing: 0.9px;
    -webkit-font-smoothing: antialiased;
    -webkit-transform: translateY(23px);
        -ms-transform: translateY(23px);
            transform: translateY(23px);
}

.about-page-past-section a {
    font-size: 15px;
    font-size: 600;
    color: var(--yglf-green);
    text-decoration: none;
    line-height: var(--cell);
    display: inline-block;
    letter-spacing: 3.51px;
    border-bottom: 1px solid var(--yglf-green);
}
.about-page-past-section a:hover {
    color: var(--yglf-white);
    border-bottom-color: var(--yglf-white);
}



